.product-description {
  font-family: 'Poppins-regular', sans-serif;
  padding-right: 125px;

  &__row {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }

  &__style {
    font-family: 'Poppins-medium', sans-serif;
    color: #3a54d6;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 500;
    line-height: 2.5;
    letter-spacing: 1.8px;
    margin-bottom: 19px;
  }

  &__title {
    font-family: 'Oswald-regular', sans-serif;
    font-size: 30px;
    color: #111111;
    margin-bottom: 27px;
  }
}

.reviews {
  margin-bottom: 31px;
  &__rating-stars {
    display: flex;
    margin-right: 20px;
    color: #BBBBBB;
    & .active {
      color: #feb312;
    }
  }

  &__desc {
    color: #bbbbbb;
    font-size: 12px;
  }
}

.options {
  justify-content: space-between;
  margin-bottom: 55px;
  align-items: flex-start;
  &__title {
    font-family: 'Poppins-semi-bold', sans-serif;
    color: #111111;
    font-size: 12px;
  }
}

.colors {
  margin-right: 30px;
  &__list {
    display: flex;
  }

  &__item {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin: 20px 20px 0 0;
  }
}

.sizes {

  &__list {
    display: flex;
  }

  &__item {
    margin: 20px 20px 0 0;
  }
}

.order-info {
  justify-content: space-between;
  &__price {
    font-family: 'Poppins-semi-bold', sans-serif;
    font-size: 20px;
    color: #111111;
  }

  &__old-rice {
    font-size: 14px;
    color: #b1b1b1;

  }

  &__quantity {

  }

  &__quantity-input {
    font-family: 'Poppins-semi-bold', sans-serif;
    font-size: 14px;
    text-align: center;
    color: #111111;
    width: 100px;
    height: 50px;
    padding: 19px 17px 20px;
    border: solid 1px #dddddd;
    border-radius: 25px;
  }

  &__btn-add {
    width: 130px;
    height: 50px;
    padding: 15px 39px 17px 38px;
    box-shadow: 0px 5px 20px 0 rgba(58, 84, 214, 0.4);
    border-radius: 25px;
    background-color: #3a54d6;
    font-family: 'Poppins-medium', sans-serif;
    font-size: 14px;
    text-align: center;
    color: #ffffff;
  }
}
