.slider {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px;
  height: 100vh;

  &__conteiner {
    z-index: 1;
    position: relative;
    max-width: 1255px;
    width: calc(100% - 100px - 8%);
    display: flex;
    flex-direction: row;
  }

  &__row {
    max-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-width: 150px;
  }

  &__img-box {
    width: 100%;

    &.hide {
      animation: imgBoxHide 500ms ease-in-out both;
    }

    &.show {
      animation: imgBoxShow 500ms ease-in-out both;
    }
  }

  &__img {
    width: 100%;
  }

  &__title {
    text-align: center;
    text-transform: uppercase;
    letter-spacing: -18px;
    top: 39%;
    font-size: calc(40px + 1 * 100vw / 20);
    font-family: sans-serif;
    position: absolute;
    text-align-last: justify;
    left: 50%;
    transform: translate(-50%, -50%);
    font-weight: bold;
    color: #fff;
  }

  &__desc {
  }

  &__btn-more {
    cursor: pointer;
    background-color: #5d616e;
    width: 150px;
    border-radius: 20px;
    color: #fff;
    padding: 10px 0;
  }

  &__label {
    font-size: 12px;
    font-family: sans-serif;
    background-color: #0b0b0b;
    display: block;
    padding: 5px 7px;
    color: #fff;
    text-align: center;
    border-radius: 7px;
    align-self: flex-end;
  }

  &__nav {
    position: absolute;
    width: 100%;
    height: 100%;
  }

  &__nav-dots {
    position: absolute;
    bottom: 0;
    right: 0;
    display: flex;
    flex-direction: row;
  }

  &__nav-dot {
    cursor: pointer;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #fff;
    margin-left: 8px;
    opacity: 0.3;
    transition: opacity 100ms ease-in-out;

    &:hover {
      opacity: .65;
    }

    &.active {
      opacity: unset;
    }
  }

  &__nav-btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 1px solid rgba(255, 255, 255, 0.3);
    background-color: transparent;
    color: #d7d7d7;
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover {
      border-color: rgb(255, 255, 255);
    }

    &--prev {
      left: 2%;
    }

    &--next {
      right: 2%;
    }

  }
}

@keyframes imgBoxHide {
  0% {
    opacity: unset;

  }
  100% {
    opacity: 0;
  }
}

@keyframes imgBoxShow {
  0% {
    transform: translateY(15%);
    opacity: 0;
  }
  50% {
    opacity: unset;
  }
  100% {
    opacity: unset;
    transform: translateY(0);
  }
}

